import uniSegmentedControl from "@/components/uni/uni-segmented-control/uni-segmented-control.vue"
import None from "@/components/qy/none.vue"
export default {
	data() {
		return {
			getimg:this.$getimg,
			likeList: '',
			id: '',
			list_empty: false,
			items: ['商品', '商店'],
			current: 0
		}
	},
	components: {
		None,
		uniSegmentedControl
	},
	onLoad(option) {
		this._load()
		this.id = option.id
	},
	onShow(option) {
		this._load()
	},
	methods: {
		_load() {
			this.$api.http.get('favorite/get_all_fav').then(res=>{
				// this.likeList = res.data.goods
				if(!res.data.goods){
					this.list_empty=true
				}else{
					this.likeList = res.data.goods
				}
				console.log(this.likeList)
			})
		},
		del(id) {
			
			const that = this
			uni.showModal({
				title: '提示',
				content: '确定取消？',
				success: function(res) {
					if (res.confirm) {
						that.$api.http.put('/favorite/del_fav', {
							id:id
						}).then(res => {
							that.$api.msg('取消成功')
							that._load()
							
						})
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});


		},
		jump_toporduct(id) {
			uni.navigateTo({
				url: '/pages/extend-view/productDetail/productDetail?id=' + id,
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		}
	},
	onPullDownRefresh() {
		console.log('refresh');
		this._load();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
